@import "./variables";

@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700;800&family=Nunito+Sans:wght@300;400;600;700&display=swap");
$regular-font: "Montserrat", sans-serif;
$display-font: "Nunito Sans", sans-serif;

$dark-jungle-green: #1c3738;
$outer-space-crayola: #1c3738;
$wintergreen-dream: #6f9283;
$mint-cream: #f4fff8;
$pewter-blue: #8baaad;

@import url(Nav/Nav.scss);
@import url(Login/login.scss);
@import url(Scanner/scanner.scss);
@import url(Results/results.scss);
@import url(Rewards/rewards.scss);
@import url(History/History.scss);

html {
  margin: 0;
  padding: 0;
  max-height: 100vh;
  overflow: hidden;
}

h1,
h2,
h3,
label {
  font-family: $regular-font;
  font-weight: bold;
  color: $dark-jungle-green;
}

p,
span {
  font-family: $display-font;
  color: $dark-jungle-green;
}

.error {
  color: red;
}

body {
  margin: 0;
  padding: 0;
  overflow: hidden;
  height: 100%;
}

form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 10px 20px;

  input {
    width: 200px;
    border-radius: 15px;
    padding: 9px 5px;
    border: 1px solid #1c3738;
  }

  label {
    width: 200px;
    margin-left: -5px;
  }
}

button {
  border-radius: 15px;
  width: auto;
  letter-spacing: 1px;
  border: none;
  padding: 10px 25px;
  font-size: 16px;
  font-family: $display-font;
  background-color: #1c3738;
  color: #f4fff8 !important;
  transition: all 500ms linear;
  &:disabled {
    opacity: 0.4;
  }
}

a {
  text-decoration: none;
  margin-bottom: 5px;
}

.capitalize {
  text-transform: capitalize;
}
